import { Query } from "react-apollo";
import gql from "graphql-tag";
import React from "react";
import Projectform from "../components/Projectform";
import App from '../components/App'
import Header from '../components/Header'


export const projectQuery = gql`
query projectlist($id: String!) {
  projectlist(id: $id) {
    id
    name
    description
  }
}
`;

class ProjectModule extends React.Component {
  static async getInitialProps({ query }) {
    const { id } = query;
    return { id };
  }
  render() {
    const projectQueryVars = {
      id: this.props.id
    };
    if (typeof this.props.id == "undefined") return (<App><Header /><Projectform /></App>);
    return (
      <App>
       <Header />
        <Query query={projectQuery} variables={projectQueryVars}>
          {(data, loading, error) => {
            console.log(data.data.projectlist[0].id);
            if (data.loading) return <div>Fetching</div>;
            if (data.error) return <div>Error</div>;
            return <Projectform project={data.data.projectlist} />;
          }}
        </Query>
      </App>
    );
  }
}

export default ProjectModule;